@import url("https://fonts.googleapis.com/css?family=Karla:400,400i,700");
@import url(../fontawesome/css/all.css);
:root {
  --font-size-initial:12px;
  --font-size-lv2:14px;
  --font-size-lv3:16px;
  --font-size-lv4:18px; }

.special div {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0; }

.nav-main a:after {
  display: block;
  content: ''; }

.about-us h2, .services-by h4, .staff h4, .heading h2, .gallery h2 {
  padding: 0;
  margin: 0; }

/* 
 * Whitespace Reset  */
* {
  padding: 0;
  margin: 0; }

/* 
 * Box-sizing fix */
html {
  -webkit-box-sizing: border-box;
  box-sizing: border-box; }

*, *:before, *:after {
  -webkit-box-sizing: inherit;
  box-sizing: inherit; }

html {
  font: var(--font-size-initial, 12px)/1.6 "Karla", sans-serif; }

@media screen and (min-width: 500px) {
  html {
    font-size: var(--font-size-lv2, 14px); } }

@media screen and (min-width: 700px) {
  html {
    font-size: var(--font-size-lv2, 16px); } }

@media screen and (min-width: 1600px) {
  html {
    font-size: var(--font-size-lv4, 18px); } }

body, table, th, td, form, input, textarea, option, select {
  font: inherit; }

h1, h2, h3, h4, h5, h6 {
  line-height: 1.2;
  padding-bottom: 0.5em;
  font-weight: 400;
  font-family: "Helvetica";
  text-transform: uppercase;
  margin: 1.3em 0;
  color: #e8048b; }

h1 {
  font-size: 2.6em; }

h2 {
  font-size: 2.2em; }

h3 {
  font-size: 1.8em; }

h4 {
  font-size: 1.6em; }

h5 {
  font-size: 1.5em; }

h6 {
  font-size: 1.5em; }

.button {
  padding: 1rem 4rem;
  color: #FFF;
  text-transform: uppercase;
  border: 2px solid black;
  background: black;
  border-radius: 0.3rem;
  text-decoration: none;
  font-weight: 700;
  -webkit-transition: border 0.3s ease-in-out,background 0.3s ease-in-out;
  transition: border 0.3s ease-in-out,background 0.3s ease-in-out; }
  .button:hover {
    border: 2px solid #FFF;
    background: transparent; }

.button2 {
  padding: 1rem 3rem;
  color: #FFF;
  text-transform: uppercase;
  border: 2px solid #e8048b;
  background: #e8048b;
  border-radius: 0.3rem;
  text-decoration: none;
  font-weight: 700;
  -webkit-transition: border 0.3s ease-in-out,color 0.3s ease-in-out,background 0.3s ease-in-out;
  transition: border 0.3s ease-in-out,color 0.3s ease-in-out,background 0.3s ease-in-out; }
  .button2:hover {
    border: 2px solid #e8048b;
    color: #e8048b;
    background: transparent; }

.navigation {
  margin-top: -42.5rem;
  background: rgba(0, 0, 0, 0.5);
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
  -ms-flex-pack: justify;
  justify-content: space-between;
  padding: 0 13rem;
  -ms-flex-wrap: wrap;
  flex-wrap: wrap; }

.nav-main {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex; }
  .nav-main ul {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    margin-top: 0.5rem; }
  .nav-main li {
    list-style: none outside;
    padding: 0 2rem; }
    .nav-main li:nth-child(1) a:after {
      opacity: 1; }
  .nav-main a {
    text-decoration: none;
    text-transform: uppercase;
    color: #FFF; }
    .nav-main a:after {
      height: 0.18rem;
      background: rgba(155, 144, 139, 0.9);
      width: 1.8rem;
      margin: 0 auto;
      border-radius: 0.2rem;
      margin-top: 0.5rem;
      opacity: 0; }
    .nav-main a:hover:after {
      opacity: 1; }

.site-header {
  background: url("../images/Beauty-Courses.jpg");
  background-size: cover;
  background-repeat: no-repeat;
  padding-top: 43.5rem;
  background-position: 55% 25%;
  background-position-y: 55%; }

.site-branding {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-item-align: stretch;
  align-self: stretch;
  position: relative;
  top: -1rem;
  margin-bottom: -1rem; }
  .site-branding img {
    background: white;
    padding: 1rem 1.5rem;
    width: auto;
    height: 100%; }

.location {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  background: #b3016a;
  color: #FFF;
  -webkit-box-pack: space-evenly;
  -ms-flex-pack: space-evenly;
  justify-content: space-evenly;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  padding: 2.5rem 3rem; }
  .location article {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    justify-content: center; }
  .location span {
    display: block; }
    .location span:nth-child(2) {
      font-weight: bold; }
  .location i {
    font-size: 3rem;
    margin-right: 1.5rem; }

.about-us {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
  background: #fbf8fb;
  padding: 4rem 11rem; }
  .about-us h2 {
    font-weight: 700;
    padding-bottom: 2rem; }

.text-side,
.img-side {
  -ms-flex-preferred-size: 50%;
  flex-basis: 50%;
  padding: 2rem 0;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center; }

.text-side {
  padding: 2rem 2rem;
  color: #5f5f5f; }
  .text-side p {
    padding-bottom: 5rem; }

.img-side {
  display: grid;
  grid-template-columns: 19rem 19rem;
  grid-gap: 2rem;
  padding-top: 4rem; }
  .img-side img {
    width: 100%;
    height: auto; }

/* SPECIAL OFFER SECTION */
.special {
  background: -webkit-gradient(linear, left top, left bottom, from(rgba(0, 0, 0, 0.8)), to(rgba(0, 0, 0, 0.8))), url("../images/background1.jpg");
  background: linear-gradient(rgba(0, 0, 0, 0.8), rgba(0, 0, 0, 0.8)), url("../images/background1.jpg");
  padding-top: 22rem;
  background-position: 80% 40%;
  background-size: cover;
  background-repeat: no-repeat;
  position: relative; }
  .special div {
    color: #FFF;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    justify-content: center;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
    -ms-flex-direction: column;
    flex-direction: column;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center; }
  .special p {
    font-size: 2.2rem;
    font-weight: bold; }
    .special p:nth-child(1) span {
      font-weight: 700;
      font-size: 3.5rem; }
    .special p:nth-child(2) span {
      font-size: 1.7rem;
      font-weight: normal; }
    .special p:nth-child(2) span:nth-child(2) {
      font-size: 1.2rem; }
  .special a {
    margin-top: 1.8rem; }

/* OUR SERVICES SECTION */
.services {
  text-align: center;
  color: #5f5f5f;
  padding: 0 10rem;
  background: #fbf8fb; }
  .services h2 {
    font-weight: 700;
    padding-bottom: 0; }
  .services div {
    padding: 3rem 0; }
    .services div a {
      margin: 0 0.5rem; }
      .services div a:nth-child(1) {
        padding: 1rem 1.5rem; }
      .services div a:nth-child(2) {
        padding: 1rem 2.5rem; }

.services-by {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-rows: 1fr 1fr;
  padding-top: 4.5rem;
  grid-gap: 2rem;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center; }
  .services-by article {
    background: #FFF;
    border: 1px solid #e2e2e2;
    padding: 2rem;
    border-radius: 0.2rem; }
    .services-by article img {
      border-radius: 50%; }
  .services-by h4 {
    font-weight: bold;
    padding: 1.5rem 0 1rem 0; }
  .services-by p {
    padding-bottom: 2rem; }

/* STAFF SECTION */
.staff {
  background: -webkit-gradient(linear, left top, left bottom, from(rgba(0, 0, 0, 0.8)), to(rgba(0, 0, 0, 0.8))), url("../images/background2.jpg");
  background: linear-gradient(rgba(0, 0, 0, 0.8), rgba(0, 0, 0, 0.8)), url("../images/background2.jpg");
  background-size: cover;
  background-position: 50% 10%;
  background-repeat: no-repeat;
  color: #FFF;
  margin-top: 2rem;
  text-align: center;
  position: relative; }
  .staff .members {
    display: grid;
    grid-template-columns: repeat(4, 20rem);
    grid-gap: 2rem;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    justify-content: center;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center; }
  .staff h2 {
    color: #FFF; }
  .staff article {
    background: #FFF;
    color: #5f5f5f;
    margin-bottom: 5rem; }
    .staff article div {
      text-align: left;
      padding: 1.5rem; }
  .staff h4 {
    font-size: 1.2rem;
    font-weight: bold; }
  .staff img {
    width: 100%;
    height: auto; }

.job {
  font-style: italic;
  padding-bottom: 1.5rem; }

.heading {
  padding: 3rem 14rem; }
  .heading h2 {
    padding: 2.5rem 0;
    font-weight: 700; }
  .heading p {
    padding-bottom: 2rem; }

/* GALLERY SECTION */
.gallery {
  text-align: center;
  padding: 0 10rem; }
  .gallery h2 {
    margin-top: 4rem;
    padding: 2rem 0;
    font-weight: 700; }

.img-gallery {
  padding-top: 3rem;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-template-rows: 1fr 1fr; }
  .img-gallery img {
    width: 100%;
    height: auto; }
  .img-gallery img:nth-child(1) {
    grid-column-start: 1;
    grid-column-end: 3; }
